@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=PT+Sans&display=swap);

.header-select-city {
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  align-items: center;
  font-weight: bold;
}
.header-select-city img {
  padding-left: 14px;
}
.header-form-search-panel {
  width: 60px;
  height: 60px;
  background-color: #313030;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
}
.header {
  width: 100%;
  height: 140px;
  margin-top: 30px;
  background-color: #F2F2F2;
}
.header-filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 60px;
}
.header-filters-title h1 {
  font-weight: 400;
  font-size: 36px;
}
.header-form{
  display: flex;
  justify-content: space-between;
}


.header-form-filter-panel {
  width: 60px;
  height: 60px;
  background-color: #313030;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
}
.restaurant-container__card {
  width: 400px;
  height: 393px;
  background: #313030;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  margin-top: 20px;
}
.card-list-item-img {
  width: 350px;
  height: 235px;
}
.restaurant_container__card-naming {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 13px;
  font-size: 18px;
  font-weight: 800;
}
.restaurant_container__card-discription {
  display: flex;
  width: 90%;
  justify-content: space-between;
}
.restaurant_container__card-discription p{
  font-size: 16px;
  font-weight: 600;
}
.restaurant_container_card__discription-contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.restaurant_container_card__discription-contact img{
  width: 97px;
  height: 16px;
  margin-top: 10px;
}
.restaurant_container_card__discription-price{
 display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.restaurant_container_card__discription-price img{
  width: 77.13px;
  height: 17.06px;
  margin-top: 10px;
}
.restaurant_container__card-discription p, .card-list-item-title{
  margin-top: 10px;
}
.main-card-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-right: 5px;
}
.main p{
  font-family: 'Montserrat', sans-serif;
  color: white;
}
.main {
  height: 80vh;
  margin-top: 10px;
  overflow: hidden;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 5px; 
  background-color: #F2F2F2;
}
::-webkit-scrollbar-thumb {
  background-color: #313030;
  border-radius: 10px;
}
.sidebar {
  width: 130px;
  height: 100vh;
  background-color: #313030;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.logo {
  margin: 25px 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sidebar-list {
  width: 90%;
  min-height: 200px;
  align-self: end;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.sidebar-list-items {
  position: absolute;
  top: 0;
  left: 34px;
}
.sidebar-list-item {
  margin: 35px 0 70px 0;
}
.sidebar-list__active {
  height: 95px;
  background-color: #F2F2F2;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  position: relative;
  display: flex;
  align-items: center;
  top: 100px;
}
.border-one, .border-two, .block-one, .block-two {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
}
.border-one, .border-two {
  background-color: #313030;
}
.border-one, .block-one {
  top: -20px;
}
.border-two, .block-two {
  top: 95px;
}
.block-one, .block-two {
  background-color: #F2F2F2;
}
.border-one {
  border-bottom-right-radius: 30px;
}
.border-two {
  border-top-right-radius: 30px;
}
.sidebar-list__btn {
  width: 75px;
  height: 75px;
  border-radius: 20px;
  background: linear-gradient(to bottom right, #FEC163, #DE4313);
  margin-left: 10px;
}
html, body {
  margin: 0;
  padding: 0;
  background-color: #F2F2F2;
  font-family: 'PT Sans', sans-serif;
}
h1, h2, h3, p {
  font-family: 'PT Sans', sans-serif;
  margin: 0;
}
.app {
  display: flex;
}
.content-page {
  margin: 0 40px;
  width: 100%;
  height: 100vh;
}
